// lazyLoader.js - ÈÅÖÂª∂„É≠„Éº„Éá„Ç£„É≥„Ç∞„Éû„Éç„Éº„Ç∏„É£„Éº

class LazyLoader {
  constructor() {
    this.loadedModules = new Map();
    this.loadingPromises = new Map();
    this.imageObserver = null;
    this.componentObserver = null;
    this.initializeImageLazyLoading();
    this.initializeComponentLazyLoading();
  }

  /**
   * „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´„ÇíÈÅÖÂª∂„É≠„Éº„Éâ
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   * @returns {Promise<Object>} „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´
   */
  async loadPageModule(pageName) {
    const moduleKey = `page_${pageName}`;

    // Êó¢„Å´Ë™≠„ÅøËæº„ÅøÊ∏à„Åø„ÅÆÂ†¥Âêà„ÅØ„Ç≠„É£„ÉÉ„Ç∑„É•„Åã„ÇâËøî„Åô
    if (this.loadedModules.has(moduleKey)) {
      return this.loadedModules.get(moduleKey);
    }

    // Ë™≠„ÅøËæº„Åø‰∏≠„ÅÆÂ†¥Âêà„ÅØÊó¢Â≠ò„ÅÆPromise„ÇíËøî„Åô
    if (this.loadingPromises.has(moduleKey)) {
      return this.loadingPromises.get(moduleKey);
    }

    // Êñ∞„Åó„ÅÑË™≠„ÅøËæº„ÅøPromise„Çí‰ΩúÊàê
    const loadingPromise = this.createPageLoadPromise(pageName, moduleKey);
    this.loadingPromises.set(moduleKey, loadingPromise);

    try {
      const module = await loadingPromise;
      this.loadedModules.set(moduleKey, module);
      this.loadingPromises.delete(moduleKey);
      return module;
    } catch (error) {
      this.loadingPromises.delete(moduleKey);
      throw error;
    }
  }

  /**
   * „Éö„Éº„Ç∏Ë™≠„ÅøËæº„ÅøPromise„Çí‰ΩúÊàê
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   * @param {string} moduleKey - „É¢„Ç∏„É•„Éº„É´„Ç≠„Éº
   * @returns {Promise<Object>}
   */
  async createPageLoadPromise(pageName, moduleKey) {
    console.log(`üîÑ ÈÅÖÂª∂„É≠„Éº„ÉâÈñãÂßã: ${pageName}`);
    const startTime = performance.now();

    try {
      let module;

      // „Éö„Éº„Ç∏Âêç„Å´Âøú„Åò„ÅüÂãïÁöÑ„Ç§„É≥„Éù„Éº„Éà
      switch (pageName) {
        case 'dashboard':
          module = await import('../pages/dashboardPage.js');
          break;
        case 'workout':
          module = await import('../pages/workoutPageWizard.js');
          break;
        case 'calendar':
          module = await import('../pages/calendarPage.js');
          break;
        case 'analysis':
          module = await import('../pages/analysisPage.js');
          break;
        case 'progress':
          module = await import('../pages/progressPage.js');
          break;
        case 'exercises':
        case 'exercises-management':
          module = await import('../pages/exercisePage.js');
          break;
        case 'settings':
          module = await import('../pages/settingsPage.js');
          break;
        default:
          throw new Error(`Unknown page: ${pageName}`);
      }

      const loadTime = performance.now() - startTime;
      console.log(`‚úÖ ÈÅÖÂª∂„É≠„Éº„ÉâÂÆå‰∫Ü: ${pageName} (${loadTime.toFixed(2)}ms)`);

      // „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„É°„Éà„É™„ÇØ„Çπ„ÇíË®òÈå≤
      this.recordPerformanceMetric('page_load', pageName, loadTime);

      return module;
    } catch (error) {
      const loadTime = performance.now() - startTime;
      console.error(
        `‚ùå ÈÅÖÂª∂„É≠„Éº„ÉâÂ§±Êïó: ${pageName} (${loadTime.toFixed(2)}ms)`,
        error
      );
      throw error;
    }
  }

  /**
   * „Çµ„Éº„Éì„Çπ„É¢„Ç∏„É•„Éº„É´„ÇíÈÅÖÂª∂„É≠„Éº„Éâ
   * @param {string} serviceName - „Çµ„Éº„Éì„ÇπÂêç
   * @returns {Promise<Object>} „Çµ„Éº„Éì„Çπ„É¢„Ç∏„É•„Éº„É´
   */
  async loadServiceModule(serviceName) {
    const moduleKey = `service_${serviceName}`;

    if (this.loadedModules.has(moduleKey)) {
      return this.loadedModules.get(moduleKey);
    }

    if (this.loadingPromises.has(moduleKey)) {
      return this.loadingPromises.get(moduleKey);
    }

    const loadingPromise = this.createServiceLoadPromise(
      serviceName,
      moduleKey
    );
    this.loadingPromises.set(moduleKey, loadingPromise);

    try {
      const module = await loadingPromise;
      this.loadedModules.set(moduleKey, module);
      this.loadingPromises.delete(moduleKey);
      return module;
    } catch (error) {
      this.loadingPromises.delete(moduleKey);
      throw error;
    }
  }

  /**
   * „Çµ„Éº„Éì„ÇπË™≠„ÅøËæº„ÅøPromise„Çí‰ΩúÊàê
   * @param {string} serviceName - „Çµ„Éº„Éì„ÇπÂêç
   * @param {string} moduleKey - „É¢„Ç∏„É•„Éº„É´„Ç≠„Éº
   * @returns {Promise<Object>}
   */
  async createServiceLoadPromise(serviceName, moduleKey) {
    console.log(`üîÑ „Çµ„Éº„Éì„ÇπÈÅÖÂª∂„É≠„Éº„ÉâÈñãÂßã: ${serviceName}`);
    const startTime = performance.now();

    try {
      let module;

      switch (serviceName) {
        case 'chart':
          module = await import('../services/chartService.js');
          break;
        case 'exercise':
          module = await import('../services/exerciseService.js');
          break;
        case 'recommendation':
          module = await import('../services/recommendationService.js');
          break;
        case 'progressTracking':
          module = await import('../services/progressTrackingService.js');
          break;
        default:
          throw new Error(`Unknown service: ${serviceName}`);
      }

      const loadTime = performance.now() - startTime;
      console.log(
        `‚úÖ „Çµ„Éº„Éì„ÇπÈÅÖÂª∂„É≠„Éº„ÉâÂÆå‰∫Ü: ${serviceName} (${loadTime.toFixed(2)}ms)`
      );

      this.recordPerformanceMetric('service_load', serviceName, loadTime);
      return module;
    } catch (error) {
      const loadTime = performance.now() - startTime;
      console.error(
        `‚ùå „Çµ„Éº„Éì„ÇπÈÅÖÂª∂„É≠„Éº„ÉâÂ§±Êïó: ${serviceName} (${loadTime.toFixed(2)}ms)`,
        error
      );
      throw error;
    }
  }

  /**
   * ÁîªÂÉè„ÅÆÈÅÖÂª∂„É≠„Éº„Éá„Ç£„É≥„Ç∞„ÇíÂàùÊúüÂåñ
   */
  initializeImageLazyLoading() {
    // Intersection Observer API „Çí„Çµ„Éù„Éº„Éà„Åó„Å¶„ÅÑ„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
    if (!('IntersectionObserver' in window)) {
      console.warn(
        'IntersectionObserver not supported, falling back to immediate loading'
      );
      this.loadAllImagesImmediately();
      return;
    }

    this.imageObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            this.loadImage(entry.target);
            this.imageObserver.unobserve(entry.target);
          }
        });
      },
      {
        rootMargin: '50px 0px', // 50pxÊâãÂâç„ÅßË™≠„ÅøËæº„ÅøÈñãÂßã
        threshold: 0.01,
      }
    );

    // Êó¢Â≠ò„ÅÆÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°ÁîªÂÉè„ÇíÁõ£Ë¶ñ
    this.observeExistingImages();
  }

  /**
   * Êó¢Â≠ò„ÅÆÁîªÂÉè„ÇíÁõ£Ë¶ñÂØæË±°„Å´ËøΩÂä†
   */
  observeExistingImages() {
    const lazyImages = document.querySelectorAll(
      'img[data-src], img[loading="lazy"]'
    );
    lazyImages.forEach((img) => {
      this.imageObserver.observe(img);
    });
  }

  /**
   * Êñ∞„Åó„ÅÑÁîªÂÉè„ÇíÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„Å´ËøΩÂä†
   * @param {HTMLImageElement} img - ÁîªÂÉèË¶ÅÁ¥†
   */
  observeImage(img) {
    if (this.imageObserver && img) {
      this.imageObserver.observe(img);
    }
  }

  /**
   * ÁîªÂÉè„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLImageElement} img - ÁîªÂÉèË¶ÅÁ¥†
   */
  loadImage(img) {
    const startTime = performance.now();

    // data-src „Åã„Çâ src „Å´ÁßªÂãï
    if (img.dataset.src) {
      img.src = img.dataset.src;
      img.removeAttribute('data-src');
    }

    // Ë™≠„ÅøËæº„ÅøÂÆå‰∫ÜÊôÇ„ÅÆÂá¶ÁêÜ
    img.onload = () => {
      const loadTime = performance.now() - startTime;
      img.classList.add('loaded');
      console.log(`üì∑ ÁîªÂÉèË™≠„ÅøËæº„ÅøÂÆå‰∫Ü: ${img.src} (${loadTime.toFixed(2)}ms)`);
      this.recordPerformanceMetric('image_load', img.src, loadTime);
    };

    // Ë™≠„ÅøËæº„ÅøÂ§±ÊïóÊôÇ„ÅÆÂá¶ÁêÜ
    img.onerror = () => {
      const loadTime = performance.now() - startTime;
      img.classList.add('error');
      console.error(
        `‚ùå ÁîªÂÉèË™≠„ÅøËæº„ÅøÂ§±Êïó: ${img.src} (${loadTime.toFixed(2)}ms)`
      );

      // „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÁîªÂÉè„ÇíË®≠ÂÆö
      if (!img.src.includes('default-avatar')) {
        img.src = '/assets/default-avatar.png';
      }
    };
  }

  /**
   * ÂÖ®ÁîªÂÉè„ÇíÂç≥Â∫ß„Å´Ë™≠„ÅøËæº„ÅøÔºà„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÔºâ
   */
  loadAllImagesImmediately() {
    const lazyImages = document.querySelectorAll('img[data-src]');
    lazyImages.forEach((img) => this.loadImage(img));
  }

  /**
   * „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆÈÅÖÂª∂„É≠„Éº„Éá„Ç£„É≥„Ç∞„ÇíÂàùÊúüÂåñ
   */
  initializeComponentLazyLoading() {
    if (!('IntersectionObserver' in window)) {
      return;
    }

    this.componentObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            this.loadComponent(entry.target);
            this.componentObserver.unobserve(entry.target);
          }
        });
      },
      {
        rootMargin: '100px 0px', // 100pxÊâãÂâç„ÅßË™≠„ÅøËæº„ÅøÈñãÂßã
        threshold: 0.01,
      }
    );
  }

  /**
   * „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„Å´ËøΩÂä†
   * @param {HTMLElement} element - Ë¶ÅÁ¥†
   * @param {string} componentType - „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„Çø„Ç§„Éó
   */
  observeComponent(element, componentType) {
    if (this.componentObserver && element) {
      element.dataset.componentType = componentType;
      this.componentObserver.observe(element);
    }
  }

  /**
   * „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLElement} element - Ë¶ÅÁ¥†
   */
  async loadComponent(element) {
    const componentType = element.dataset.componentType;
    if (!componentType) {
      return;
    }

    const startTime = performance.now();
    console.log(`üîÑ „Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÈÅÖÂª∂„É≠„Éº„ÉâÈñãÂßã: ${componentType}`);

    try {
      switch (componentType) {
        case 'chart':
          await this.loadChartComponent(element);
          break;
        case 'calendar':
          await this.loadCalendarComponent(element);
          break;
        case 'progress-chart':
          await this.loadProgressChartComponent(element);
          break;
        default:
          console.warn(`Unknown component type: ${componentType}`);
      }

      const loadTime = performance.now() - startTime;
      console.log(
        `‚úÖ „Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÈÅÖÂª∂„É≠„Éº„ÉâÂÆå‰∫Ü: ${componentType} (${loadTime.toFixed(2)}ms)`
      );
      this.recordPerformanceMetric('component_load', componentType, loadTime);
    } catch (error) {
      const loadTime = performance.now() - startTime;
      console.error(
        `‚ùå „Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÈÅÖÂª∂„É≠„Éº„ÉâÂ§±Êïó: ${componentType} (${loadTime.toFixed(2)}ms)`,
        error
      );
    }
  }

  /**
   * „ÉÅ„É£„Éº„Éà„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLElement} element - „ÉÅ„É£„Éº„ÉàË¶ÅÁ¥†
   */
  async loadChartComponent(element) {
    const chartService = await this.loadServiceModule('chart');
    const chartType = element.dataset.chartType || 'line';
    const chartData = JSON.parse(element.dataset.chartData || '{}');

    if (chartService.default) {
      chartService.default.createChart(element.id, chartType, chartData);
    }
  }

  /**
   * „Ç´„É¨„É≥„ÉÄ„Éº„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLElement} element - „Ç´„É¨„É≥„ÉÄ„ÉºË¶ÅÁ¥†
   */
  async loadCalendarComponent(element) {
    const calendarPage = await this.loadPageModule('calendar');
    if (calendarPage.default && calendarPage.default.renderCalendar) {
      calendarPage.default.renderCalendar(element);
    }
  }

  /**
   * „Éó„É≠„Ç∞„É¨„Çπ„ÉÅ„É£„Éº„Éà„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLElement} element - „Éó„É≠„Ç∞„É¨„Çπ„ÉÅ„É£„Éº„ÉàË¶ÅÁ¥†
   */
  async loadProgressChartComponent(element) {
    const progressService = await this.loadServiceModule('progressTracking');
    const chartData = JSON.parse(element.dataset.chartData || '{}');

    if (progressService.default) {
      progressService.default.renderProgressChart(element, chartData);
    }
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„É°„Éà„É™„ÇØ„Çπ„ÇíË®òÈå≤
   * @param {string} type - „É°„Éà„É™„ÇØ„Çπ„Çø„Ç§„Éó
   * @param {string} name - ÂêçÂâç
   * @param {number} duration - ÂÆüË°åÊôÇÈñì
   */
  recordPerformanceMetric(type, name, duration) {
    const metric = {
      type,
      name,
      duration,
      timestamp: Date.now(),
    };

    // LocalStorage„Å´‰øùÂ≠òÔºàÊúÄÊñ∞100‰ª∂„Åæ„ÅßÔºâ
    try {
      const metrics = JSON.parse(
        localStorage.getItem('performanceMetrics') || '[]'
      );
      metrics.push(metric);

      // ÊúÄÊñ∞100‰ª∂„ÅÆ„Åø‰øùÊåÅ
      if (metrics.length > 100) {
        metrics.splice(0, metrics.length - 100);
      }

      localStorage.setItem('performanceMetrics', JSON.stringify(metrics));
    } catch (error) {
      console.warn('Failed to save performance metric:', error);
    }

    // „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÈñæÂÄ§„ÉÅ„Çß„ÉÉ„ÇØ
    this.checkPerformanceThreshold(type, duration);
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÈñæÂÄ§„Çí„ÉÅ„Çß„ÉÉ„ÇØ
   * @param {string} type - „É°„Éà„É™„ÇØ„Çπ„Çø„Ç§„Éó
   * @param {number} duration - ÂÆüË°åÊôÇÈñì
   */
  checkPerformanceThreshold(type, duration) {
    const thresholds = {
      page_load: 1000, // 1Áßí
      service_load: 500, // 0.5Áßí
      image_load: 3000, // 3Áßí
      component_load: 800, // 0.8Áßí
    };

    const threshold = thresholds[type];
    if (threshold && duration > threshold) {
      console.warn(
        `‚ö†Ô∏è „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπË≠¶Âëä: ${type} „ÅåÈñæÂÄ§(${threshold}ms)„ÇíË∂Ö„Åà„Åæ„Åó„Åü: ${duration.toFixed(2)}ms`
      );

      // „Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´„Åó„Å¶„É¢„Éã„Çø„É™„É≥„Ç∞
      window.dispatchEvent(
        new CustomEvent('performanceWarning', {
          detail: { type, duration, threshold },
        })
      );
    }
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„É°„Éà„É™„ÇØ„Çπ„ÇíÂèñÂæó
   * @returns {Array} „É°„Éà„É™„ÇØ„ÇπÈÖçÂàó
   */
  getPerformanceMetrics() {
    try {
      return JSON.parse(localStorage.getItem('performanceMetrics') || '[]');
    } catch (error) {
      console.warn('Failed to load performance metrics:', error);
      return [];
    }
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁµ±Ë®à„ÇíÂèñÂæó
   * @returns {Object} Áµ±Ë®àÊÉÖÂ†±
   */
  getPerformanceStats() {
    const metrics = this.getPerformanceMetrics();
    const stats = {};

    // „Çø„Ç§„ÉóÂà•„Å´Áµ±Ë®à„ÇíË®àÁÆó
    ['page_load', 'service_load', 'image_load', 'component_load'].forEach(
      (type) => {
        const typeMetrics = metrics.filter((m) => m.type === type);
        if (typeMetrics.length > 0) {
          const durations = typeMetrics.map((m) => m.duration);
          stats[type] = {
            count: typeMetrics.length,
            avg: durations.reduce((a, b) => a + b, 0) / durations.length,
            min: Math.min(...durations),
            max: Math.max(...durations),
            recent: typeMetrics.slice(-10).map((m) => m.duration),
          };
        }
      }
    );

    return stats;
  }

  /**
   * „Ç≠„É£„ÉÉ„Ç∑„É•„Çí„ÇØ„É™„Ç¢
   */
  clearCache() {
    this.loadedModules.clear();
    this.loadingPromises.clear();
    console.log('üßπ ÈÅÖÂª∂„É≠„Éº„ÉÄ„Éº„Ç≠„É£„ÉÉ„Ç∑„É•„Çí„ÇØ„É™„Ç¢„Åó„Åæ„Åó„Åü');
  }

  /**
   * „Ç™„Éñ„Ç∂„Éº„Éê„Éº„ÇíÁ†¥Ê£Ñ
   */
  destroy() {
    if (this.imageObserver) {
      this.imageObserver.disconnect();
      this.imageObserver = null;
    }

    if (this.componentObserver) {
      this.componentObserver.disconnect();
      this.componentObserver = null;
    }

    this.clearCache();
    console.log('üóëÔ∏è ÈÅÖÂª∂„É≠„Éº„ÉÄ„Éº„ÇíÁ†¥Ê£Ñ„Åó„Åæ„Åó„Åü');
  }
}

// „Ç∑„É≥„Ç∞„É´„Éà„É≥„Ç§„É≥„Çπ„Çø„É≥„Çπ„Çí„Ç®„ÇØ„Çπ„Éù„Éº„Éà
export const lazyLoader = new LazyLoader();
